@import url("https://fonts.googleapis.com/css?family=Open+Sans");
.slides {
  background-color: #f6f6f6; }
  .slides-video {
    padding: 15% 0;
    overflow: hidden;
    position: relative;
    width: 100%; }
    .slides-video video {
      position: absolute;
      width: 100%;
      top: 0; }
  .slides .carousel .control-dots {
    padding: 0; }
  .slides .carousel .slide {
    cursor: -webkit-grab; }
    .slides .carousel .slide.selected .slideContent {
      background-size: 100%; }
    .slides .carousel .slide:active {
      cursor: -webkit-grabbing; }
    .slides .carousel .slide .slideContent {
      padding: 15% 0;
      background: #f6f6f6;
      background-position: center; }
      @media (max-width: 767px) {
        .slides .carousel .slide .slideContent {
          padding: 30% 0; } }



