@import url("https://fonts.googleapis.com/css?family=Open+Sans");
.tablelist {
  padding: 0 0 40px; }
  .tablelist .tablelist-record-wrapper {
    padding: 40px 0 20px; }
    .tablelist .tablelist-record-wrapper .tablelist-record-single {
      display: inline-block; }
  .tablelist .tablelist-record-title {
    font-weight: bold;
    padding: 0 20px;
    background-color: #f6f6f6; }
  .tablelist .tablelist-record {
    padding: 0 20px;
    background-color: #f6f6f6; }
    .tablelist .tablelist-record:last-child .tablelist-record-wrapper {
      border-bottom: 0; }



