@import url("https://fonts.googleapis.com/css?family=Open+Sans");
.grid {
  text-align: left; }
  .grid .grid_items .grid_items_inner {
    width: 100%;
    height: 100%;
    cursor: pointer;
    display: table;
    background-color: #fff;
    -webkit-transition: 0.1s;
    -o-transition: 0.1s;
    transition: 0.1s;
    border: 1px solid #fff; }
    .grid .grid_items .grid_items_inner:hover {
      -webkit-box-shadow: 4px 5px 10px 0px #f6f6f6;
              box-shadow: 4px 5px 10px 0px #f6f6f6;
      border: 1px solid #555; }
    .grid .grid_items .grid_items_inner .grid_items_inner_content {
      background-size: cover;
      width: 100%;
      height: 100%;
      background-position: center;
      color: #fff;
      display: table-cell;
      vertical-align: middle; }
    .grid .grid_items .grid_items_inner .grid_items_inner_img {
      background-size: cover;
      height: 100%;
      width: 100%;
      background-position: center; }



